import React from "react";
import { Helmet } from "react-helmet";
import PropTypes from "prop-types";
import { graphql, useStaticQuery } from "gatsby";

import Header from "./header";


function Layout({ children }) {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  return (
    <div className="flex flex-col font-sans min-h-screen text-gray-900">
      <Helmet>
        <script src="https://kit.fontawesome.com/bb76f7eca9.js"></script>
      </Helmet>

      <Header siteTitle={data.site.siteMetadata.title} />

      {/* 
      <main className="flex flex-col flex-1 md:justify-center max-w-6xl mx-auto px-4 py-8 md:p-8 w-full">
        {children}
      </main>
      */}
      {children}

      <footer className="bg-blue-700 fixed bottom-0 w-full">
        <p className="leading-loose my-3 text-center">
          <a href="mailto:taeil2@gmail.com" className="text-white font-medium mx-5"><i className="fas fa-envelope mr-1"></i> Email</a>
          <a href="https://www.linkedin.com/in/taeil2/" className="text-white font-medium mx-5"><i className="fab fa-linkedin mr-1"></i> LinkedIn</a>
          <a href="/taeil-kwak-resume.pdf" className="text-white font-medium mx-5"><i className="fas fa-file-alt mr-1"></i> Resume</a>
        </p>
      </footer>
      <Helmet>
        <script src="https://cdnjs.cloudflare.com/ajax/libs/animejs/2.0.2/anime.min.js"></script>
      </Helmet>
    </div>
  );
}

Layout.propTypes = {
  children: PropTypes.node.isRequired
};

export default Layout;
