import { Link } from "gatsby";
import PropTypes from "prop-types";
import {Component} from "react";
import React, { useState } from "react";

class Header extends Component {
  constructor(props) {
    super(props);
  }
  
  componentDidMount() {
    window.onscroll = function() {
      var d = document.documentElement;

      var top = d.scrollTop;
      var windowHeight = window.innerHeight;
      var offset = d.scrollTop + window.innerHeight;
      var height = d.offsetHeight;

      /*
      console.log('top = ' + top);
      console.log('windowHeight = ' + windowHeight);
      console.log('offset = ' + offset);
      console.log('height = ' + height);
      */

      var mainNav = document.getElementById("mainNav");
      // sticky header
      if (top > 0){
        mainNav.classList.add("sticky");
      }
      else{
        mainNav.classList.remove("sticky");
      }
    }

    document.addEventListener('click', function (event) {

      // If the clicked element doesn't have the right selector, bail
      if (!event.target.matches('.main-nav-link')) return;
    
      // Don't follow the link
      event.preventDefault();
    
      // Log the clicked element in the console
      console.log(event.target);

      var href = event.target.getAttribute("href");
      href = href.replace(/^#/, '');
      document.getElementById(href).scrollIntoView({behavior: 'smooth'});

    
    }, false);
  }

  render() {
    return (
      <nav id="mainNav" className="flex content-center bg-blue-700 hidden">
      <div className="flex flex-wrap items-center justify-between max-w-5xl mx-auto w-full px-5 lg:px-0">
        <Link to="/" className="flex items-center no-underline">
          <h1 className="logo tracking-tight">Taeil Kwak</h1>
        </Link>

        {/*
        <button
          className="block md:hidden border border-white flex items-center px-3 py-2 rounded text-white"
          onClick={() => toggleExpansion(!isExpanded)}
        >
          <svg
            className="fill-current h-3 w-3"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <title>Menu</title>
            <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
          </svg>
        </button>

        <div
          className={`${
            isExpanded ? `block` : `hidden`
          } md:block md:flex md:items-center w-full md:w-auto`}
        >
        */}

        <div
          className="block flex items-center w-auto"
        >
          <div className="main-nav">
            <a href="#work-section-anchor"
              className="inline-block mt-0 no-underline text-md main-nav-link"
            >
              Work
            </a>
            {/*
            <a href="#contact-section-anchor"
              className="block md:inline-block mt-4 md:mt-0 ml-6 no-underline text-white main-nav-link"
            >
              Contact
            </a>
            */}
          </div>
        </div>
      </div>
    </nav>
  );
  }
}


